$(function () {

	// WOW.js
	new WOW({
		boxClass: 'wow',
		animateClass: 'animated',
		offset: 0,
		mobile: false,
		live: true
	}).init();

	const headerMain = $('.header');
	const headerMainScrollTrigger = 50; //.header:top(px)
	const headerNav = $('.header__nav');
	const menuBtn = $('.menu-btn');
	const testimonialsSlider = $('.testimonials__slider');

	let scrollOffset = 0;
	$(window).on("scroll", function () {
		scrollOffset = $(this).scrollTop();

		if ($(this).innerWidth() > 767) {
			headerOnScroll();
		}

		function headerOnScroll() {
			if (scrollOffset >= headerMainScrollTrigger) {
				headerMain.addClass('scroll');
			} else {
				headerMain.removeClass('scroll');
				headerNav.removeClass('show');
				$('body').removeClass('body-fixed');
			}
		}
	});

	menuBtn.click(function (event) {
		$(this).toggleClass('active');
		headerNav.toggleClass('show');
		$('body').toggleClass('body-fixed');
	});
	testimonialsSlider.slick({
		arrows: true,
		dots: true,
		infinite: false,
		centerMode: false,
		variableWidth: true,
		speed: 400,
		slidesToShow: 1,
		slidesToScroll: 1,
		responsive: [
			{
				breakpoint: 992,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					variableWidth: false
				}
			},
			{
				breakpoint: 576,
				settings: {
					slidesToShow: 1,
					slidesToScroll: 1,
					variableWidth: false,
					arrows: false
				}
			}
		]
	});

	smoothScroll($('[data-nav-link]'));

});